import React, { useState, useEffect } from 'react';
import axios from 'axios';

function StudentsEdit() {

    const [surname, setSurname] = useState('zxc')
    const [name, setName] = useState('zcv')
    const [patronymic, setPatronymic] = useState('zxv')
    const [course, setCourse] = useState(11)
    const [group, setGroup] = useState('zxc')

    return (
        <form onSubmit={() => {
            alert({
                surname: surname,
                name: name,
                patronymic: patronymic,
                course: course,
                group: group
            });
            axios({
                method: "POST",
                url: 'http://127.0.0.1:8000/api/students/',
                data: {
                    surname: surname,
                    name: name,
                    patronymic: patronymic,
                    course: course,
                    group: group
                }
            })
        }}>
            <input type="text" name="surname" onChange={e => setSurname(e.value)} placeholder="Фамилия" />
            <input type="text" name="name" onChange={e => setName(e.value)} placeholder="Фамилия" />
            <input type="text" name="patronymic" onChange={e => setPatronymic(e.value)} placeholder="Фамилия" />
            <input type="number" name="course" onChange={e => setCourse(e.value)} placeholder="Фамилия" />
            <input type="text" name="group" onChange={e => setGroup(e.value)} placeholder="Фамилия" />
            <input type="submit" value="Отправить" />
        </form>
    );
}

export default StudentsEdit;